import React, {useState, useRef} from 'react';
import * as d3 from 'd3'
import './App.css';

import {Layout, Row, Col, Form, Radio, ConfigProvider, Select, Space, Slider, InputNumber, Button, Tour} from 'antd';
const { Option } = Select;
const { Header, Footer, Sider, Content } = Layout;
import 'antd/dist/reset.css';
import {SisternodeOutlined} from '@ant-design/icons';

import Draggable, {DraggableCore} from 'react-draggable'; // Both at the same time





import View from "./Components/View";
import OriginCircuit from "./Components/OriginCircuit";
import Dandelion from "./Components/Dandelion";
import Legend from "./Components/Legend";




const HEADER_HEIGHT = 34, WHOLE_MARGIN_LR = 100


//整体布局的CSS代码放在这里，其余的细节的样式放在 App.css 里面
const root_comp_style = {
    "headerStyle": {
        width: '1220px',
        color: '#ffffff',
        margin: 'auto auto 9px',
        height: `${HEADER_HEIGHT}px`,
        lineHeight: `${HEADER_HEIGHT-14}px`,
        backgroundColor: '#333b3e',
        paddingTop: '8px',
        paddingBottom: '10px',
        // paddingInline: WHOLE_MARGIN_LR
    },


    "contentStyle": {
        height: "100%",
        border: "solid 1px #515962",
        borderRadius: "12px",
        padding: "8px"
    }
}



function App() {


    // 控制 选择的算法
    // const [param_algo, setAlgo] = useState('qiskit_grover_2q')
    const [param_algo, setAlgo] = useState('grover_n2_QASMBench')
    const [statevector, setStatevector] = useState([])
    const [theta, setTheta] = useState(1)
    const [point_radius, setPoint_radius] = useState(1)
    const [disable_slider, setDisable_slider] = useState(true)


    //导览所用变量
    const ref1 = useRef(null);
    const ref2 = useRef(null);
    const ref3 = useRef(null);
    const ref4 = useRef(null);
    const [open, setOpen] = useState(true);

    const steps = [
        {
            title: 'QuantumEyes System',
            description: 'We propose three visualizations below to support the analysis of quantum states. Also, we proposed Dandelion Chart (via clicking the background of steps) to explain the quantum states regarding amplitudes. A 1920x1080 resolution monitor is recommended.',
            cover: (
                <img
                    alt="tour.png"
                    src="https://www.boldbi.com/wp-content/uploads/2019/07/data-visualization-importance-featured.webp"
                />
            ),
            target: () => ref1.current,
        },
        {
            title: 'Probability Summary View',
            description: `Providing a summary of the algorithm's behavior based on the measured probability. By brushing the area, State Evolution View will be rendered.`,
            cover: (
                <img
                    alt="tour2.png"
                    src="https://coursebuffet.com/course_images/coursera/probability.png"
                />
            ),
            target: () => d3.select('.view1').node(),
        }
        ,
        {
            title: 'Clear the selection',
            description: `Clear the brushed area via this button`,

            target: () => ref3.current,
        }
        ,
        {
            title: 'Dandelion chart control',
            description: `By dragging the slider, mitigating the scalability issue of the dandelion chart by scaling all circles' area.`,

            target: () => ref2.current,
        },
        {
            title: 'Original quantum circuit',
            description: `Here is the original quantum circuit you must be familiar with.\nNow Enjoy!`,

            target: () => d3.select('#originalCircuit_container').node(),
        }]


    function handleValueChange(event){

        // setAlgo(event.target.value)
        setAlgo(event)

    }


    function change_statevector(item1, item2, e) {
        setStatevector([item1, item2, e])
    }


    function change_theta(value){
        setTheta(value)
    }

    function change_pointRadius(value){
        setPoint_radius(value)
    }

    function selection_clear(){
        d3.select('.selection').style('display', 'none')

        // 删除view3
        d3.selectAll('.view3')
            .remove()

        d3.selectAll('.path_animation')
            .remove()

        d3.select('.view2')
            .remove()

        d3.selectAll('.view3_line')
            .remove()

        d3.selectAll('.bundle_container ')
            .remove()

        document.getElementById('dandelion_container').style.visibility='hidden'
    }









    return (

        <>
            {/* <Tour open={open} onClose={() => setOpen(false)} steps={steps}></Tour> */}

            <ConfigProvider theme={{ hashed: false }}>


                <Layout style={{ height: '1100px'}}>
                    <Content style={root_comp_style.contentStyle}>
                        <Header style={root_comp_style.headerStyle}>
                            <div  ref={ref1} >
                                <SisternodeOutlined  style={{fontSize: '1.3em'}}/>
                                <span className="system-title">QuantumEyes</span>
                                <span className="paper-title">QuantumEyes: Towards Better Interpretability of Quantum Circuits</span>
                            </div>

                        </Header>

                        {/* control panel*/}
                        <Row  justify="center">
                            <Col style={{width: '98%'}}>
                                <div className={'component control_panel'}>

                                    <Form size={'small'} layout={'inline'}>

                                        {/*<Form.Item style={{paddingLeft: '30px', marginRight: '30px'}}>*/}
                                        {/*    <i className="fas fa-pen view_title_icon" style={{fontSize: '1.5em'}}/>*/}
                                        {/*    <span className={'view_title_text'} style={{fontSize: '1.8em'}}> Control panel </span>*/}
                                        {/*</Form.Item>*/}


                                        <Space size={20}>


                                            <Form.Item label="Algo select"
                                                       style={{ width: 180 }}
                                            >
                                                <Select placeholder="Please select an algorithm"
                                                        defaultValue={param_algo}
                                                        onChange={handleValueChange}
                                                        style={{ width: 100 }}
                                                >
                                                    <Option value="grover_n2_QASMBench">Grover-n2 QASMBench</Option>
                                                    <Option value="qft_n3_Qiskit">QFT-n3 Qiskit</Option>
                                                    {/*<Option value="qiskit_grover_2q">Grover-n2 Qiskit</Option>*/}
                                                </Select>
                                            </Form.Item>


                                            <Form.Item label="View1 selection">
                                                <Button ref={ref3} size={'small'} onClick={selection_clear}>Clear</Button>
                                            </Form.Item>



                                            {/*<Form.Item label="Form Layout"  >*/}
                                            {/*    <Radio.Group>*/}
                                            {/*        <Radio.Button value="horizontal">Horizontal</Radio.Button>*/}
                                            {/*        <Radio.Button value="vertical">Vertical</Radio.Button>*/}
                                            {/*        <Radio.Button value="inline">Inline</Radio.Button>*/}
                                            {/*    </Radio.Group>*/}
                                            {/*</Form.Item>*/}


                                            <Form.Item label="Dandelion point:" style={{width: '300px'}}>
                                                <Row>


                                                    <Col>
                                                        <Slider
                                                            step={0.2}
                                                            min={1}
                                                            max={2}
                                                            style={{width: '100px'}}
                                                            defaultValue={1}
                                                            // min={this.state.view2_qual_extent[0]}
                                                            // max={this.state.view2_qual_extent[1]}
                                                            // onAfterChange={setTheta}
                                                            onChange={change_pointRadius}
                                                            // disabled={check1()}
                                                        />
                                                    </Col>
                                                    &nbsp;&nbsp;&nbsp;
                                                    <Col>
                                                        <InputNumber
                                                            min={1}
                                                            max={2}
                                                            value={point_radius}
                                                            controls={false}                                                        onChange={change_theta}
                                                            onChange={change_pointRadius}
                                                            style={{width: '50px'}}
                                                        />
                                                    </Col>

                                                </Row>

                                            </Form.Item>




                                            <Form.Item  label="Dandelion circle:" style={{width: '300px'}}>
                                                <Row>


                                                    <Col>
                                                        <Slider
                                                            step={0.01}
                                                            min={0}
                                                            max={1}
                                                            style={{width: '100px'}}
                                                            defaultValue={1}
                                                            // min={this.state.view2_qual_extent[0]}
                                                            // max={this.state.view2_qual_extent[1]}
                                                            // onAfterChange={setTheta}
                                                            onChange={change_theta}
                                                            disabled={disable_slider}
                                                        />
                                                    </Col>
                                                    &nbsp;&nbsp;&nbsp;
                                                    <Col>
                                                        <InputNumber ref={ref2}
                                                            /*min={1}
                                                            max={20}*/
                                                            value={theta}
                                                            controls={false}                                                        onChange={change_theta}
                                                            onChange={change_theta}
                                                            style={{width: '50px'}}
                                                        />
                                                    </Col>

                                                </Row>

                                            </Form.Item>


                                        </Space>


                                    </Form>

                                                  
                            {/*    把 Legend 放这里*/}
                            <Legend
                                param_algo={param_algo}
                            ></Legend>

                                </div>
                            </Col>
                            
              
                        </Row>


                        {/* View 层*/}
                        <Row  gutter={0} justify="space-around" style={{height: "82%"}}>


                            <Col span={16}>

                                <Row  gutter={30}  justify="center" style={{height: "72%", marginBottom: '20px'}}>
                                    <div   className={'component'} style={{width: '95%', height: "100%"}}>

                                        <View
                                            param_algo={param_algo}
                                            change_statevector={change_statevector}
                                            param_dandelion={{
                                                param_algo:param_algo,
                                                statevector:statevector,
                                                theta:theta,
                                                point_radius:point_radius,
                                                func_disable_slider:setDisable_slider
                                            }}
                                        ></View>


                                        {/* <Dandelion
                                            param_algo={param_algo}
                                            statevector={statevector}
                                            theta={theta}
                                            point_radius={point_radius}
                                            func_disable_slider={setDisable_slider}
                                        ></Dandelion> */}
      

                                        
                                    </div>

                                </Row>

                                {/* <Draggable> */}
                                    {/* <div> */}
                                        <Row gutter={30} justify="center" style={{ height: "25%" }}>
                                            <div className={'component'} style={{ width: '95%', height: "186px" }}>


                                                <OriginCircuit
                                                    param_algo={param_algo}
                                                ></OriginCircuit>


                                            </div>
                                        </Row>
                                    {/* </div> */}
                                {/* </Draggable> */}

                            </Col>


                        </Row>

                    </Content>
                </Layout>






            </ConfigProvider>
        </>









  );
}

export default App;
